// components/Board.tsx
import { Board, Position } from '@/types/types';
import { FaDotCircle, FaRegDotCircle } from 'react-icons/fa';
import { FaCircle } from 'react-icons/fa6';

interface BoardProps {
    board: Board;
    onCellClick: (row: number, col: number) => void;
    selectedPiece: Position | null;
    currentPlayer: number; // 1 for black, 2 for white
    validMoves: Position[]; // List of positions where current player can move
    capturablePositions: Position[]; // List of positions where current player can capture
}

const BoardComponent: React.FC<BoardProps> = ({
    board,
    onCellClick,
    selectedPiece,
    currentPlayer,
    validMoves,
    capturablePositions
}) => {
    return (
        <div className="grid grid-cols-5 gap-4 w-[600px] h-[600px] mx-auto rounded-xl bg-[#FFFFFF] p-6 shadow-2xl ring-2 ring-gray-900/10">
            {board.map((row, rowIndex) =>
                row.map((cell, colIndex) => {
                    const isCurrentPlayerPiece = cell === currentPlayer;
                    const hasValidMove = validMoves.some(([r, c]) => r === rowIndex && c === colIndex);
                    const hasCapturableMove = capturablePositions.some(([r, c]) => r === rowIndex && c === colIndex);

                    return (
                        <div
                            key={`${rowIndex}-${colIndex}`}
                            className={`flex items-center justify-center w-16 h-16 rounded-lg cursor-pointer transition-all duration-200 
                                ${cell === 1 ? 'bg-black hover:bg-gray-900' :
                                    cell === 2 ? 'bg-gray-100 hover:bg-gray-200' :
                                        'bg-gray-300 hover:bg-gray-400'} 
                                ${selectedPiece && selectedPiece[0] === rowIndex && selectedPiece[1] === colIndex ? 'border-2 border-blue-500' : ''} 
                                ${isCurrentPlayerPiece && hasValidMove ? 'outline outline-4 outline-yellow-500' : ''} 
                                ${isCurrentPlayerPiece && hasCapturableMove ? 'outline outline-4 outline-red-500' : ''}`} // Red outline for capturable moves
                            onClick={() => onCellClick(rowIndex, colIndex)}
                        >
                            {cell === 1 && <FaRegDotCircle className="text-white text-3xl" />}
                            {cell === 2 && <FaCircle className="text-black text-3xl" />}
                        </div>
                    );
                })
            )}
        </div>
    );
};

export default BoardComponent;
